import React from 'react';
import { useMutation, useQuery, useQueryClient } from '@tanstack/react-query';
import { useFormik } from 'formik';
import * as yup from 'yup';

import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Divider from '@mui/material/Divider';
import LocalShippingIcon from '@mui/icons-material/LocalShipping';
import FactoryIcon from '@mui/icons-material/Factory';
import PersonIcon from '@mui/icons-material/Person';

import StopsForm from './stops';

import {
  Loader,
  SearchField,
  SearchMultiField,
  SelectMultiField,
  TextField,
  SelectField,
  FileList,
} from '../components';

import {
  LoadUpdate,
  LoadQuery,
  UsersQuery,
  TrucksQuery,
  CustomersQuery,
} from '../api';
import { useSnackbar } from '../hooks';
import {
  UpdateLoad,
  TruckTypes,
  User,
  Truck,
  LoadStatuses,
  LoadStatus,
  StopPickUpStatuses,
  StopDeliveryStatuses,
  Customer,
  StopType,
  UnitsOfWeight,
  UnitsOfLength,
  TimeFrameType,
  FormOnClose,
} from '../utils/dataTypes';
import { cleanObject } from '../utils/queryHelpers';

const timeFramePickUpValidation = yup.object({
  type: yup
    .string()
    .oneOf([TimeFrameType.FCFS, TimeFrameType.ASAP, TimeFrameType.APPT])
    .required('Type is required'),
  at: yup.date().when(['type'], (type, schema) => {
    return (type[0] as unknown as TimeFrameType) === TimeFrameType.FCFS
      ? schema.oneOf([undefined])
      : schema.required('At is required');
  }),
  from: yup.date().when(['type'], (type, schema) => {
    return (type[0] as unknown as TimeFrameType) !== TimeFrameType.FCFS
      ? schema.oneOf([undefined])
      : schema.required('From is required');
  }),
  to: yup.date().when(['type'], (type, schema) => {
    return (type[0] as unknown as TimeFrameType) !== TimeFrameType.FCFS
      ? schema.oneOf([undefined])
      : schema.required('To is required');
  }),
});

const timeFrameDeliveryValidation = yup.object({
  type: yup
    .string()
    .oneOf([TimeFrameType.FCFS, TimeFrameType.Direct, TimeFrameType.APPT])
    .required('Type is required'),
  at: yup.date().when(['type'], (type, schema) => {
    return (type[0] as unknown as TimeFrameType) === TimeFrameType.FCFS
      ? schema.oneOf([undefined])
      : schema.required('At is required');
  }),
  from: yup.date().when(['type'], (type, schema) => {
    return (type[0] as unknown as TimeFrameType) !== TimeFrameType.FCFS
      ? schema.oneOf([undefined])
      : schema.required('From is required');
  }),
  to: yup.date().when(['type'], (type, schema) => {
    return (type[0] as unknown as TimeFrameType) !== TimeFrameType.FCFS
      ? schema.oneOf([undefined])
      : schema.required('To is required');
  }),
});

const freightValidationSchema = yup.object({
  pieces: yup.number().integer().min(1).required('Pieces must be at least 1'),
  unitOfWeight: yup
    .string()
    .oneOf(UnitsOfWeight)
    .required('Unit of weight is required'),
  weight: yup.number().moreThan(0).required('Weight is required'),
  unitOfLength: yup
    .string()
    .oneOf(UnitsOfLength)
    .required('Unit of length is required'),
  length: yup.number().moreThan(0).required('Length is required'),
});

const stopPickUpDriversInfoValidationSchema = yup.object({
  bol: yup.string().required('Bol is required'),
  pieces: yup.number().integer().min(1).required('Pieces must be at least 1'),
  unitOfWeight: yup
    .string()
    .oneOf(UnitsOfWeight)
    .required('Unit of weight is required'),
  weight: yup.number().moreThan(0).required('Weight is required'),
  seal: yup.string().required('Seal is required'),
  addressIsCorrect: yup.boolean().required('Address Is Correct is required'),
});

const stopDeliveryDriversInfoValidationSchema = yup.object({
  bol: yup.string().required('Bol is required'),
  signedBy: yup.string().required('Signed By is required'),
});

const stopPickUpValidationSchema = yup.object({
  status: yup
    .string()
    .oneOf(StopPickUpStatuses)
    .required('Stop Pick Up Status is required'),
  facility: yup
    .object({ id: yup.string().required() })
    .typeError('Enter correct facility')
    .required('Facility is required'),
  timeFrame: timeFramePickUpValidation.required('Time frame is required'),
  freightList: yup
    .array()
    .of(freightValidationSchema.required('Freight is required'))
    .required('Stop pickup must contain al least one freight'),
  driversInfo: yup
    .array()
    .of(
      stopPickUpDriversInfoValidationSchema.required(
        'Stop PickUp Drivers Info is required',
      ),
    )
    .optional(),
  addInfo: yup.string(),
});

const stopDeliveryValidationSchema = yup.object({
  status: yup
    .string()
    .oneOf(StopDeliveryStatuses)
    .required('Stop Delivery Status is required'),
  facility: yup
    .object({ id: yup.string().required() })
    .typeError('Enter correct facility')
    .required('Facility is required'),
  timeFrame: timeFrameDeliveryValidation.required('Time frame is required'),
  driversInfo: yup
    .array()
    .of(
      stopDeliveryDriversInfoValidationSchema.required(
        'Stop Delivery Drivers Info is required',
      ),
    )
    .optional(),
  bolList: yup
    .array()
    .of(yup.string().required('Bol Id must not be empty'))
    .min(1, 'Must be at least one Freight in Freight List')
    .required('Must be at least one Freight in Freight List'),
  addInfo: yup.string(),
});

const validationSchema = yup.object({
  status: yup.string().oneOf(LoadStatuses).required('Status is required'),
  ref: yup
    .array()
    .of(yup.string().required('Ref must not be empty'))
    .min(1, 'REF is required')
    .max(3, 'REF must contain not more then 3 items')
    .required('REF is required'),
  stops: yup
    .array()
    .of(
      yup.lazy((val) => {
        switch (val.type) {
          case StopType.PickUp:
            return stopPickUpValidationSchema.required(
              'Stop pickup is required',
            );
          case StopType.Delivery:
            return stopDeliveryValidationSchema.required(
              'Stop delivery is required',
            );
          default:
            return yup
              .mixed()
              .oneOf([undefined])
              .required('Type of stop must be "Pick Up" or "Delivery"');
        }
      }),
    )
    .min(2)
    .required('Stops are required'),
  weight: yup.string().required('Weight is required'),
  truckType: yup
    .array(yup.string().oneOf(TruckTypes))
    .min(1, 'Select at list one')
    .required('Truck Type is required'),
  rate: yup.number().typeError('Rate must be a number'),
  totalCharges: yup
    .number()
    .typeError('Total charges must be a number')
    .required('Total charges is required'),
  currency: yup.string().required('Currency is required'),
  bookedByUser: yup
    .object({ id: yup.string().required() })
    // .typeError('Enter correct user')
    .required('Booked by is required'),
  bookedByCompany: yup.string(),
  assignTo: yup
    .array(
      yup
        .object({ id: yup.string().required() })
        .typeError('Enter correct user'),
    )
    .min(1, 'Assign To is required')
    .required('Assign To is required'),
  checkInAs: yup.string(),
  truck: yup
    .object({ id: yup.string().required() })
    .typeError('Enter correct truck')
    .nullable(),
  bookedWith: yup
    .object({ id: yup.string().required() })
    .typeError('Enter correct customer')
    .required('Booked with is required'),
});

const startValues = {
  id: '',
  status: 'Available' as LoadStatus,
  ref: [],
  stops: [],
  weight: undefined,
  truckType: [],
  rate: undefined,
  totalCharges: undefined,
  currency: 'USD',
  bookedByUser: null,
  bookedByCompany: undefined,
  assignTo: [],
  checkInAs: '4ULogistics',
  truck: null,
  bookedWith: null,
};

export default function EditLoadForm({
  id,
  onClose,
}: {
  id: string;
  onClose: FormOnClose;
}) {
  const [initialValues, setInitialValues] =
    React.useState<UpdateLoad>(startValues); // ToDo do like this in all forms (edit and view)
  const queryClient = useQueryClient();
  const setSnackbarMessage = useSnackbar();

  const {
    isLoading: loading,
    isFetching: fetching,
    error,
    data,
  } = useQuery({
    queryKey: ['load', id],
    queryFn: LoadQuery,
    retry: false,
  });
  React.useEffect(() => {
    if (!error) {
      return;
    }
    setSnackbarMessage({
      severity: 'error',
      message: error instanceof Error ? error.message : JSON.stringify(error),
    });
  }, [error]);

  const { mutate, isLoading } = useMutation(LoadUpdate, {
    onSuccess: (data) => {
      setSnackbarMessage({
        severity: 'success',
        message: 'Saved',
      });
      queryClient.invalidateQueries(['load', id]);
      queryClient.invalidateQueries(['loads']);
      onClose('Save', data);
    },
    onError: (error: Error) => {
      setSnackbarMessage({
        severity: 'error',
        message: error instanceof Error ? error.message : JSON.stringify(error),
      });
    },
  });

  const formik = useFormik<UpdateLoad>({
    initialValues,
    enableReinitialize: true,
    validationSchema: validationSchema,
    onSubmit: (values) => {
      mutate(values);
    },
  });

  React.useEffect(() => {
    setInitialValues({
      ...initialValues,
      ...cleanObject(data, Object.keys(initialValues)),
    });
  }, [data]);

  /*const handleSubmit = (e: React.FormEvent<HTMLFormElement>) => {
    console.error(formik.errors);
    formik.handleSubmit(e);
  };*/

  const handleClose = () => {
    onClose('Close');
  };

  /*const handleReset = (e: any) => {
    formik.handleReset(e);
  };*/

  return (
    <Box sx={{ height: '100%' }}>
      <form
        onSubmit={formik.handleSubmit}
        onReset={formik.handleReset}
        style={{ height: '100%' }}
      >
        <Box sx={{ p: 3, height: '100%' }}>
          <Divider />
          <Box
            key="controls"
            sx={{
              maxHeight: 'calc(100% - 70px)',
              overflowX: 'hidden',
              overflowY: 'scroll',
            }}
          >
            <Box key="topControls" sx={{ marginBottom: -1, p: 1 }}>
              <SelectField
                fieldName="status"
                label="Status"
                items={LoadStatuses}
                /*disabledItems={LoadStatuses.filter(
                (status) =>
                  status !== 'Available' && status !== 'Not Available',
              )}*/
                formik={formik}
              />
              <TextField
                fieldName="ref"
                label="REF"
                multiline
                maxRows={3}
                formik={formik}
              />
              <StopsForm fieldName="stops" formik={formik} />
            </Box>
            <Box
              key="centerControls"
              sx={{ display: 'inline-flex', width: '100%' }}
            >
              <Box key="left" sx={{ p: 1, width: '50%' }}>
                <TextField fieldName="weight" label="Weight" formik={formik} />
                <SelectMultiField
                  fieldName="truckType"
                  label="Truck Type"
                  items={TruckTypes}
                  formik={formik}
                />
                <SearchField<User>
                  fieldName="bookedByUser"
                  label="Booked By"
                  getSelectedLabel={(selectedItem) =>
                    `${selectedItem.fullName}`
                  }
                  getItemLabel={(item) => `${item.fullName}`}
                  getItemSecondLabel={(item) => `${item.jobTitle}`}
                  ListIcon={PersonIcon}
                  entityType="User"
                  queryFn={UsersQuery}
                  orderby="fullName"
                  order="asc"
                  formik={formik}
                />
                <SearchField<Customer>
                  fieldName="bookedWith"
                  label="Booked With"
                  getSelectedLabel={(selectedItem) => `${selectedItem.name}`}
                  getItemLabel={(item) => `${item.name}`}
                  getItemSecondLabel={(item) => `${item.type}`}
                  ListIcon={FactoryIcon}
                  entityType="Customer"
                  queryFn={CustomersQuery}
                  orderby="name"
                  order="asc"
                  formik={formik}
                />
                <SearchField<Truck>
                  fieldName="truck"
                  label="Truck"
                  getSelectedLabel={(selectedItem) =>
                    `${selectedItem.truckNumber}`
                  }
                  getItemLabel={(item) => `${item.truckNumber}`}
                  getItemSecondLabel={(item) =>
                    `${item.status}, ${item.licencePlate}`
                  }
                  ListIcon={LocalShippingIcon}
                  entityType="Truck"
                  queryFn={TrucksQuery}
                  orderby="truckNumber"
                  order="asc"
                  formik={formik}
                />
              </Box>
              <Box key="right" sx={{ p: 1, width: '50%' }}>
                <TextField
                  fieldName="rate"
                  label="Drivers Rate"
                  placeholder="Enter drivers rate"
                  number
                  formik={formik}
                />
                <TextField
                  fieldName="totalCharges"
                  label="Total Rate"
                  placeholder="Enter total rate"
                  number
                  formik={formik}
                />
                <TextField
                  fieldName="currency"
                  label="Currency"
                  formik={formik}
                />
                <TextField
                  fieldName="checkInAs"
                  label="Check In As"
                  formik={formik}
                />
                <TextField
                  fieldName="bookedByCompany"
                  label="Booked By Company"
                  formik={formik}
                />
              </Box>
            </Box>
            <Box
              key="bottomControls"
              sx={{ p: 1, paddingBottom: 0, paddingTop: 0 }}
            >
              <SearchMultiField<User>
                fieldName="assignTo"
                label="Assign To"
                getSelectedLabel={(selectedItem) => `${selectedItem.fullName}`}
                getItemLabel={(item) => `${item.fullName}`}
                getItemSecondLabel={(item) => `${item.jobTitle}`}
                ListIcon={PersonIcon}
                queryFn={UsersQuery}
                orderby="fullName"
                order="asc"
                formik={formik}
              />
              <Box key="spacerRate" sx={{ paddingBottom: 2, paddingTop: 2 }} />
              <FileList
                objectId={data?.id}
                objectType="Load"
                label="Rate Files"
                tags={{ [`${data?.id}`]: 'Rate' }}
              />
              <Box key="spacerBill" sx={{ paddingBottom: 2, paddingTop: 2 }} />
              <FileList
                objectId={data?.id}
                objectType="Load"
                label="Bill Files"
                tags={{ [`${data?.id}`]: 'Bill' }}
              />
            </Box>
          </Box>
          <Divider />
          <Box key="buttons" sx={{ paddingTop: 3, display: 'flex' }}>
            <Box key="left" sx={{ p: 1, paddingTop: 0, width: '50%' }}>
              <Button
                fullWidth
                variant="contained"
                size="small"
                color="primary"
                type="submit"
                disabled={!formik.dirty}
              >
                Save
              </Button>
            </Box>
            <Box key="right" sx={{ p: 1, paddingTop: 0, width: '50%' }}>
              <Button
                fullWidth
                variant="contained"
                size="small"
                color="primary"
                type="button"
                onClick={handleClose}
              >
                Cancel
              </Button>
            </Box>
          </Box>
        </Box>
      </form>
      <Loader visible={isLoading || loading || fetching} />
    </Box>
  );
}
